import React from "react";
import "./NotFound.scss";
export default function NotFound() {
     return (
          <div className="Not">
               <span className="Not__text">Không tìm thấy trang này</span>
               <p onClick={() => window.history.back()} className="Not__link">
                    Trở về
               </p>
          </div>
     );
}
